.app{
  --bg-color-1:#9db3ed;
  --bg-color-2:#f9f9f9;
  --text-color-1:#0b1537;
  --text-color-2:#636363;

}
.app[data-theme="dark"]{
  --bg-color-1:#172d67;
  --bg-color-2:#050c26;
  --text-color-1:#fbfcfc;
  --text-color-2:#bbbcbc; 
}
.custom-scroll::-webkit-scrollbar{
  height: 5px;
  width: 5px;
}

.custom-scroll::-webkit-scrollbar-track{
 background-color: transparent;
}

.custom-scroll::-webkit-scrollbar-thumb{
  background-color: rgba(0, 0, 0, 0.15);
}

.custom-scroll::-webkit-scrollbar-thumb:hover{
  background-color:gray;

}
.roll-scroll::-webkit-scrollbar-thumb:hover{
  background-color: red;
}
.app{
  background: #2a6c84;
  height: 100vh;
  display: flex;
justify-content:  center;
align-items: center;
}

.app_calculator{
height: 500px;
width: 250px;
overflow: hidden;
border-radius: 30px;
position: relative;
box-shadow: 1px -1px 20px rgba(0,0,0,0.15);
display: flex;
flex-direction: column;
}

.app_calculator_navbar{
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color:  transparent;
  min-height: 40px;
  padding: 10px;
  display: flex;
  gap: 10px;
  align-items: center;
}
.app_calculator_navbar img{
  height: 24px;
  width: 24px;
}
.app_calculator_navbar_toggle{
  height: 15px;
  width: 30px;
  border-radius:30px;
  box-shadow: 1px 1px 40px 80px rgba(0,0,0,0.14);
}
.app_calculator_navbar_toggle_circle{
  height: 15px;
  width:  15px;
  border-radius: 50%;
  transition: 300ms ;
  background-color: #ede8e8;
}

.app_calculator_navbar_toggle_circle_active{
  margin-left: 15px;
}